.item{
display: none;
 background-color: rgba(7, 10, 10,0.9);
  width: 104px;
  height: 10%;
  margin: 1px;
  color: white;
  position: relative;
}
a{
  text-decoration: none;
  color: white;
}

.item_content{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 100%;

}
.item::after{
  content:'';
  display: block;
  width: 0%;
  height: 3px;
  border-radius: 50px;
position: absolute;
z-index: 1;
margin-top: -5px;
}

.item:hover::after {
  content:'';
  -webkit-animation: link 0.5s forwards;
          animation: link 0.5s forwards;  
  background:  rgb(13, 229, 236);

}

