@-webkit-keyframes link{
  0% {  width: 0%;;
  border-radius: 50px;}
  100% {width: 100%;
    border-radius: 50px;}
  }





@keyframes link{
  0% {  width: 0%;;
  border-radius: 50px;}
  100% {width: 100%;
    border-radius: 50px;}
  }
@-webkit-keyframes menu{
  0% {  left: -5%;;
}
  100% {left: 1%;
    }
  }
@keyframes menu{
  0% {  left: -5%;;
}
  100% {left: 1%;
    }
  }  


